import React from "react"
import { useStaticQuery, graphql } from "gatsby"
import Img from "gatsby-image/withIEPolyfill"
import AniLink from "gatsby-plugin-transition-link/AniLink"

import styles from "./WorkPageContent.module.scss"
import ButtonLink from "./ButtonLink"

export default () => {
  const {
    pine,
    mockupHippoMeowV1,
    mockupHippoMeowV2,
    mockupMicroLector,
    mockupTitanNews,
    iconArrowGreen,
    iconArrowYellow,
  } = useStaticQuery(graphql`
    query {
      pine: file(relativePath: { eq: "pine.png" }) {
        childImageSharp {
          fixed(height: 80) {
            ...GatsbyImageSharpFixed
          }
        }
      }
      mockupHippoMeowV1: file(relativePath: { eq: "mockupHippoMeowV1.png" }) {
        childImageSharp {
          fixed(height: 400) {
            ...GatsbyImageSharpFixed
          }
        }
      }
      mockupHippoMeowV2: file(relativePath: { eq: "mockupHippoMeowV2.png" }) {
        childImageSharp {
          fixed(height: 400) {
            ...GatsbyImageSharpFixed
          }
        }
      }
      mockupMicroLector: file(relativePath: { eq: "mockupMicroLector.png" }) {
        childImageSharp {
          fixed(height: 400) {
            ...GatsbyImageSharpFixed
          }
        }
      }
      mockupTitanNews: file(relativePath: { eq: "mockupTitanNews.png" }) {
        childImageSharp {
          fixed(height: 400) {
            ...GatsbyImageSharpFixed
          }
        }
      }
      iconArrowGreen: file(relativePath: { eq: "iconArrowGreen.png" }) {
        childImageSharp {
          fixed(width: 60) {
            ...GatsbyImageSharpFixed
          }
        }
      }
      iconArrowYellow: file(relativePath: { eq: "iconArrowYellow.png" }) {
        childImageSharp {
          fixed(width: 60) {
            ...GatsbyImageSharpFixed
          }
        }
      }
    }
  `)

  return (
    <React.Fragment>
      <div className={styles.headingContainer}>
        <div className={styles.headingContent}>
          <Img
            fixed={pine.childImageSharp.fixed}
            objectFit="cover"
            objectPosition="50% 50%"
            alt=""
          />
          <div className={styles.heading}>My Work</div>
          <Img
            fixed={pine.childImageSharp.fixed}
            objectFit="cover"
            objectPosition="50% 50%"
            alt=""
            imgStyle={{
              WebkitTransform: "scaleX(-1)",
              transform: "scaleX(-1)",
            }}
          />
        </div>
      </div>
      <div className={`${styles.projectRow} ${styles.backgroundWhite}`}>
        <div className={styles.projectRowContent}>
          <Img
            fixed={mockupHippoMeowV1.childImageSharp.fixed}
            objectFit="cover"
            objectPosition="50% 50%"
            alt=""
          />
          <div className={styles.projectInfoSeparator} />
          <div className={styles.projectInfo}>
            <div>
              I work on an idea for a family cake and cafe shop website. My aim
              is to bring a fresh and friendly atmosphere for the homepage.
            </div>
            <AniLink fade className={styles.projectCtaLink} to="/HippoMeowV1">
              <Img
                fixed={iconArrowGreen.childImageSharp.fixed}
                objectFit="cover"
                objectPosition="50% 50%"
                alt=""
              />
              <span className={styles.projectCtaLinkText}>More Details</span>
            </AniLink>
          </div>
        </div>
      </div>
      <div className={`${styles.projectRow} ${styles.backgroundYellow}`}>
        <div className={styles.projectRowContent}>
          <div className={styles.projectInfo}>
            <div>
              The homepage imitates the interface of a shop in reality with wood
              walls, lean-to, signboard, and window. This brings a more
              traditional and cozier atmosphere for the UI.
            </div>
            <AniLink fade className={styles.projectCtaLink} to="/HippoMeowV2">
              <Img
                fixed={iconArrowGreen.childImageSharp.fixed}
                objectFit="cover"
                objectPosition="50% 50%"
                alt=""
              />
              <span className={styles.projectCtaLinkText}>More Details</span>
            </AniLink>
          </div>
          <div className={styles.projectInfoSeparator} />
          <Img
            fixed={mockupHippoMeowV2.childImageSharp.fixed}
            objectFit="cover"
            objectPosition="50% 50%"
            alt=""
          />
        </div>
      </div>
      <div className={`${styles.projectRow} ${styles.backgroundGreen}`}>
        <div className={styles.projectRowContent}>
          <Img
            fixed={mockupMicroLector.childImageSharp.fixed}
            objectFit="cover"
            objectPosition="50% 50%"
            alt=""
          />
          <div className={styles.projectInfoSeparator} />
          <div className={styles.projectInfo}>
            <div>
              I redesign an existing education websign homepage. Service logos
              and details, as well as attractive information, have been added.
            </div>
            <AniLink fade className={styles.projectCtaLink} to="/MicroLector">
              <Img
                fixed={iconArrowYellow.childImageSharp.fixed}
                objectFit="cover"
                objectPosition="50% 50%"
                alt=""
              />
              <span
                className={`${styles.projectCtaLinkText} ${styles.projectCtaLinkTextYellow}`}
              >
                More Details
              </span>
            </AniLink>
          </div>
        </div>
      </div>
      <div className={`${styles.projectRow} ${styles.backgroundWhite}`}>
        <div className={styles.projectRowContent}>
          <div className={styles.projectInfo}>
            <div>
              This is the homepage of an online newspaper. It is a big challenge
              for alignment and content arrangement.
            </div>
            <AniLink fade className={styles.projectCtaLink} to="/TitanNews">
              <Img
                fixed={iconArrowGreen.childImageSharp.fixed}
                objectFit="cover"
                objectPosition="50% 50%"
                alt=""
              />
              <span className={styles.projectCtaLinkText}>More Details</span>
            </AniLink>
          </div>
          <div className={styles.projectInfoSeparator} />
          <Img
            fixed={mockupTitanNews.childImageSharp.fixed}
            objectFit="cover"
            objectPosition="50% 50%"
            alt=""
          />
        </div>
      </div>
      <div className={styles.moreWorksSection}>
        <div className={styles.moreWorksSectionContent}>
          <div className={styles.moreWorksGreenLine} />
          <div className={styles.moreWorksButtonContainer}>
            <ButtonLink to="/404" title="More Works" />
          </div>
          <div className={styles.moreWorksGreenLine} />
        </div>
      </div>
    </React.Fragment>
  )
}
